@use 'variables' as v;
@use 'mixins' as m;

html{
    font-size: 62.5%;
    box-sizing: border-box;
}
*,
*:before,
*:after{
    box-sizing: inherit;
}

body{
    font-family: v.$fuente_principal;
    font-size: 1.6rem;
    background-color: v.$back;
    &.fijar-body{
        overflow: hidden;
    }
}

p{
    font-size: 2rem;
    color: v.$secundario;
    line-height: 1.5;
}

.contenedor{
    width: 95%;
    margin: 0 auto;
    @include m.tablet {
        width: 80%;
        max-width: 120rem;
    }
}

a{
    text-decoration: none;
}
h1, h2, h3{
    margin: 0 0 5rem 0;
}
h1{
    font-size: 4rem;
}
h2{
    font-size: 3.6rem;
}
h3{
    font-size: 3rem;
}

img{
    max-width: 100%;
    width: 100%;
    height: auto;
    display: block;
}
