@use 'base/mixins' as m;
@use 'base/variables' as v;

.boletos{
    padding: 5rem 0;
    h3{
        text-align: center;
        color: v.$blanco;
        margin-bottom: 2rem;
    }
    .contenedor-boletos{
        @include m.tablet {
            @include m.grid(2, 5rem)
        }
    }
}

.pase{
    p{
        color: v.$secundario;
        text-align: center;
        margin-right: 1rem;
        margin-bottom: 0;
        font-size: 2rem;
        font-weight: 700;
        
        @include m.tablet {
            font-size: 2.6rem
        }
    }

    &-header,
    &-footer{
        p{
            text-align: center;
            color: v.$back;
            padding: 1.2rem;
            margin: 0;
            text-decoration: none;
        }
    }
    &-header{
        background-color: v.$rosa;
        text-transform: uppercase;
        border-radius: 1rem 1rem 0 0;
        p{
            font-size: 1.7rem;
        }
    }
    &-body{
        background-color: v.$blanco;
        ul{
        
            margin: 0;
            padding: 0;
            list-style: none;
            li, :nth-child(odd){
                background-color: v.$blanco;
                padding: 1.3rem;
                border-bottom: 1px solid #e1e1e1;
            }
        }
    }
    &-footer{
        background-color: v.$secundario;
        border-radius: 0 0 1rem 1rem;
        p{
            font-size: 2.5rem;
            font-weight: 700;
            letter-spacing: .5rem;
        }
    }
}