@use 'variables' as v;

@mixin telefono {
    @media (min-width: v.$telefono){
        @content;
    }
}
@mixin tablet {
    @media (min-width: v.$tablet){
        @content;
    }
}

@mixin escritorio {
    @media (min-width: v.$escritorio){
        @content;
    }
}

@mixin escritorio {
    @media (min-width: v.$escritorioxl){
        @content;
    }
}

@mixin grid($columnas, $separacion) {
    display: grid;
    grid-template-columns: repeat($columnas, 1fr);
    gap: $separacion;
}