html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Monserrat", sans-serif;
  font-size: 1.6rem;
  background-color: #f7f1e3;
}
body.fijar-body {
  overflow: hidden;
}

p {
  font-size: 2rem;
  color: #1b1b3d;
  line-height: 1.5;
}

.contenedor {
  width: 95%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .contenedor {
    width: 80%;
    max-width: 120rem;
  }
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

.header {
  z-index: 10;
  background-color: #1b1b3d;
  position: fixed;
  top: -1rem;
  left: 0;
  width: 100%;
}
@media (min-width: 768px) {
  .header {
    top: 0;
  }
}
.header .title {
  color: #fff;
  text-align: left;
  margin: 0 1rem;
  font-size: 2.9rem;
  padding: 2rem 0 2rem 0;
  background-color: #1b1b3d;
}
.header .title:first-letter {
  font-size: 4rem;
  color: #fa5c00;
}
@media (min-width: 768px) {
  .header .title {
    font-size: 4rem;
    text-align: center;
    margin: 0;
    padding: 2rem;
  }
}
.header .contenido-header {
  width: 100%;
  height: auto;
}
@media (min-width: 768px) {
  .header .contenido-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.header .contenido-header svg {
  position: fixed;
  top: 1.8rem;
  right: 0.5rem;
  z-index: 1;
}
@media (min-width: 768px) {
  .header .contenido-header svg {
    display: none;
  }
}

.navegacion-principal {
  background-color: #1b1b3d;
  width: 100%;
  height: auto;
  padding-top: 5rem;
  position: absolute;
  top: -22rem;
  left: 0;
  flex-direction: column;
  transition: all 0.4s ease-in-out;
  z-index: -1;
}
@media (min-width: 768px) {
  .navegacion-principal {
    position: initial;
    display: flex;
    flex-direction: row;
    margin: 0 5rem 0 auto;
    padding-top: 0;
    gap: 2rem;
    z-index: 1;
    width: auto;
  }
}
.navegacion-principal a {
  color: #fff;
  font-size: 2.4rem;
  transition: all 0.2s ease-in-out;
  display: block;
  text-align: center;
  margin: 1rem 0;
  padding: 2rem;
}
@media (min-width: 768px) {
  .navegacion-principal a {
    margin: 0;
    padding: 0;
  }
}
.navegacion-principal a:hover {
  color: #fa5c00;
}

.active {
  top: 4rem;
}

.img-princilap {
  position: relative;
  max-height: 58rem;
  min-height: 50rem;
  overflow: hidden;
}
.img-princilap .overlay {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.768627451);
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.img-princilap img {
  height: 100vh;
  width: 100%;
  margin-top: 6rem;
  display: block;
  object-fit: cover;
  object-position: bottom;
}

.contenido-hero {
  z-index: 1;
  margin: 0 auto 2rem auto;
}
.contenido-hero h2 {
  color: #fff;
  font-size: 4rem;
  font-weight: 900;
  line-height: 1;
  margin: 0 0 1rem 0;
}
@media (min-width: 768px) {
  .contenido-hero h2 {
    margin: 0 0 0 -6px;
  }
}
.contenido-hero:first-letter {
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
  background-image: linear-gradient(to right, #fa5c00, #ff0b0b);
  font-size: 6rem;
  margin: 0 0 0 -5px;
}
@media (min-width: 390px) {
  .contenido-hero:first-letter {
    font-size: 10rem;
  }
}
@media (min-width: 768px) {
  .contenido-hero:first-letter {
    margin: 0;
  }
}
.contenido-hero p {
  background-color: #fa5c00;
  display: inline-block;
  padding: 1rem 2rem;
  color: #fff;
  font-size: 1.6rem;
  font-weight: 700;
  margin: 0;
  border-radius: 0 20px 0 20px;
  box-shadow: inset 3px 3px 17px #d70000;
}
@media (min-width: 390px) {
  .contenido-hero p {
    font-size: 1.9rem;
  }
}

.sobre-festival {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .sobre-festival {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.contenido-festival {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .contenido-festival {
    margin-top: 0;
  }
}
.contenido-festival h2 {
  font-weight: 900;
  margin-bottom: 1rem;
}
.contenido-festival h2::first-letter {
  color: #ffb142;
}
.contenido-festival .fecha {
  color: #ff5252;
  font-weight: 700;
}

.lineup {
  background-color: #ff5252;
  padding: 5rem 0;
}
.lineup h3, .lineup p {
  text-align: center;
  color: #fff;
}
.lineup .dia {
  text-transform: uppercase;
  margin: 5rem 0;
}

@media (min-width: 768px) {
  .escenarios-contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.escenarios-contenedor p {
  color: #1b1b3d;
}

.escenario {
  padding-top: 2rem;
  border-radius: 1rem;
  box-shadow: 3px 3px 17px #333333;
}
.escenario p {
  font-weight: 900;
  font-size: 3rem;
}
.escenario p:after {
  content: "";
  display: block;
  width: 20rem;
  height: 20rem;
  background-size: 15rem;
  background-repeat: no-repeat;
  background-position: center center;
  margin: 1rem auto 0 auto;
}

.edm .nombre-escenario:after {
  background-image: url(../img/icono_edm.png);
}

.rock .nombre-escenario:after {
  background-image: url(../img/icono_rock.png);
}

.calendario {
  list-style: none;
  margin: 0;
  padding: 0;
}
.calendario li {
  padding: 2rem;
  font-weight: 700;
}
.calendario li span {
  font-weight: 400;
}

.separado {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .separado {
    margin-top: 0;
  }
}

.bg-amarillo {
  background-color: #ffb142;
}
.bg-amarillo li:nth-child(odd) {
  background-color: #f59000;
}

.bg-verde {
  background-color: #33d9b2;
}
.bg-verde li:nth-child(odd) {
  background-color: #1ea183;
}

.galeria {
  padding: 5rem 0;
  margin: 0 auto;
}
.galeria h3 {
  text-align: center;
}

.galeria-imagenes {
  list-style: none;
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 390px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 768px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 1200px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 2rem;
  }
}

.overlayek {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  margin: 0;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.8784313725);
}
@media (min-width: 768px) {
  .overlayek {
    top: 5rem;
  }
}
.overlayek picture {
  display: block;
  max-width: 70rem;
  margin: 0 2rem;
}
.overlayek .btnCerrar {
  font-size: 2.5rem;
  font-weight: 400;
  color: #ff5252;
  position: absolute;
  top: 6rem;
  right: 1.3rem;
}
@media (min-width: 768px) {
  .overlayek .btnCerrar {
    right: 24rem;
    top: 4rem;
  }
}
.overlayek .btnCerrar:hover {
  cursor: pointer;
}

.boletos {
  padding: 5rem 0;
}
.boletos h3 {
  text-align: center;
  color: #fff;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .boletos .contenedor-boletos {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.pase p {
  color: #1b1b3d;
  text-align: center;
  margin-right: 1rem;
  margin-bottom: 0;
  font-size: 2rem;
  font-weight: 700;
}
@media (min-width: 768px) {
  .pase p {
    font-size: 2.6rem;
  }
}
.pase-header p, .pase-footer p {
  text-align: center;
  color: #f7f1e3;
  padding: 1.2rem;
  margin: 0;
  text-decoration: none;
}
.pase-header {
  background-color: #ff5252;
  text-transform: uppercase;
  border-radius: 1rem 1rem 0 0;
}
.pase-header p {
  font-size: 1.7rem;
}
.pase-body {
  background-color: #fff;
}
.pase-body ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
.pase-body ul li, .pase-body ul :nth-child(odd) {
  background-color: #fff;
  padding: 1.3rem;
  border-bottom: 1px solid #e1e1e1;
}
.pase-footer {
  background-color: #1b1b3d;
  border-radius: 0 0 1rem 1rem;
}
.pase-footer p {
  font-size: 2.5rem;
  font-weight: 700;
  letter-spacing: 0.5rem;
}

.site-footer {
  background-color: #33d9b2;
  padding: 0.5rem;
}
.site-footer p {
  text-align: center;
  color: #fff;
  margin: 0;
}
.site-footer p:first-letter {
  color: #1b1b3d;
  font-weight: 700;
}