@use 'base/variables' as v;
@use 'base/mixins' as m;

.img-princilap{
    position: relative;
    max-height: 58rem;
    min-height: 50rem;
    overflow: hidden;
    .overlay{
        position: absolute;
        background-color: v.$oscuro;
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }

    img{
        height: 100vh;
        width: 100%;
        margin-top: 6rem;
        display: block;
        object-fit: cover;
        object-position: bottom;
    }
}

.contenido-hero{
    z-index: 1;
    margin: 0 auto 2rem auto;
    h2{
        color: v.$blanco;
        font-size: 4rem;
        font-weight: 900;
        line-height: 1;
        margin: 0 0 1rem 0;
        @include m.tablet {
            margin: 0 0 0 -6px
        }   
    }
    &:first-letter{
        background-clip: text;
        -webkit-background-clip: text;
        color: transparent;
        background-image: linear-gradient(to right, v.$naranja, v.$rojo);
        font-size: 6rem;
        margin: 0 0 0 -5px;
        @include m.telefono {
            font-size: 10rem;
        }
        @include m.tablet {
            margin: 0;
        }
    }
    p{
        background-color: v.$naranja;
        display: inline-block;
        padding: 1rem 2rem;
        color: v.$blanco;
        font-size: 1.6rem;
        font-weight: 700;
        margin: 0;
        border-radius: 0 20px 0 20px;
        box-shadow: inset 3px 3px 17px darken(v.$rojo, 10%);
        @include m.telefono {
            font-size: 1.9rem;
        }
    }
}