@use 'base/variables' as v;
@use 'base/mixins' as m;

.lineup {
    background-color: v.$rosa;
    padding: 5rem 0;

    h3,p{
        text-align: center;
        color: v.$blanco;
    }
    .dia{
        text-transform: uppercase;
        margin: 5rem 0;
    }
}

.escenarios-contenedor{
    @include m.tablet {
        @include m.grid(2, 5rem)
    }

    p{
        color: v.$secundario;
    }
    
}

.escenario{
    padding-top: 2rem;
    border-radius: 1rem;
    box-shadow: 3px 3px 17px #333333;
    p{
        font-weight: 900;
        font-size: 3rem;
        &:after{
            content: '';
            display: block;
            width: 20rem;
            height: 20rem;
            background-size: 15rem;
            background-repeat: no-repeat;
            background-position: center center;
            margin: 1rem auto 0 auto;
        }
    }
}

.edm .nombre-escenario:after{
    background-image: url(../img/icono_edm.png);
}

.rock .nombre-escenario:after{
    background-image: url(../img/icono_rock.png);
}

.calendario{
    list-style: none;
    margin: 0;
    padding: 0;

    li{
        padding: 2rem;
        font-weight: 700;
        span{
            font-weight: 400;
        }
    }
}
.separado{
    margin-top: 5rem;
    @include m.tablet {
        margin-top: 0;
    }
}
.bg-amarillo{
    background-color: v.$amarillo;

    li:nth-child(odd){
        background-color: darken(v.$amarillo, 15%);
    }
}
.bg-verde{
    background-color: v.$green;
    li:nth-child(odd){
        background-color: darken(v.$green, 15%);
    }
}