@use 'base/mixins' as m;
@use 'base/variables' as v;

.galeria{
    padding: 5rem 0;
    margin: 0 auto;
    h3{
        text-align: center;
    }
}
.galeria-imagenes{
    list-style: none;
    margin: 0;
    padding: 0;
    
    @include m.grid(2, 2rem);
    @include m.telefono{
        @include m.grid(3, 2rem);
    }
    @include m.tablet{
        @include m.grid(4, 2rem);
    }
    @include m.escritorio{
        @include m.grid(6, 2rem);
    }
}
.overlayek{
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
    margin: 0;

    display: grid;
    place-items: center;
    background-color: #000000e0;
    @include m.tablet {
        top: 5rem
    }
    picture{
        display: block;
        max-width: 70rem;
        // position: relative;
        margin: 0 2rem;
    }
    .btnCerrar{
        font-size: 2.5rem;
        font-weight: 400;
        color: v.$rosa;
        position: absolute;
        top: 6rem;
        right: 1.3rem;
        @include m.tablet {
            right: 24rem;
            top: 4rem;
        }
        &:hover{
            cursor: pointer;
        }
    }
}