@use 'base/variables' as v;
@use 'base/mixins' as m;


.header{
    z-index: 10;
    background-color: v.$secundario;
    position: fixed;
    top: -1rem;
    left: 0;
    width: 100%;
    
    @include m.tablet {
        top:0
    }
    .title{
        color: v.$blanco;
        text-align: left;
        margin: 0 1rem;
        font-size: 2.9rem;
        padding: 2rem 0 2rem 0;
        background-color: v.$secundario;
        &:first-letter{
            font-size: 4rem;
            color: v.$naranja;
        }
        // @include m.telefono {
        //     font-size: 2.7rem;
        // }
        @include m.tablet {
            font-size: 4rem;
            text-align: center;
            margin: 0;
            padding: 2rem;
        }
    }
    
    .contenido-header{
        width: 100%;
        height: auto;
        @include m.tablet {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
    }
    .contenido-header svg{
        position: fixed;
        top: 1.8rem;
        right: .5rem;
        z-index: 1;
        @include m.tablet {
            display: none;
        }
    }
}

.navegacion-principal{
    background-color: v.$secundario;
    width: 100%;
    height: auto;
    padding-top: 5rem;
    position: absolute;
    top: -22rem;
    left: 0;
    flex-direction: column;
    transition: all .4s ease-in-out;
    z-index: -1;

    @include m.tablet {
        position: initial;
        display: flex;
        flex-direction: row;
        margin: 0 5rem 0 auto;
        padding-top: 0;
        gap: 2rem;
        z-index: 1;
        width: auto;
    }
    
    a{
        @include m.tablet {
        margin: 0;
        padding: 0;
        }
    
        color: v.$blanco;
        font-size: 2.4rem;
        transition: all .2s ease-in-out;
        display: block;
        text-align: center;
        margin: 1rem 0;
        padding: 2rem;
        &:hover{ // & para quitar la separacion 
            color: v.$naranja;
        }
    }
}
.active{
    top: 4rem;
}
