@use 'base/mixins' as m;
@use 'base/variables' as v;

.sobre-festival{
    padding: 5rem 0; 
    @include m.tablet {
        @include m.grid(2, 5rem);
    }
}

.contenido-festival{
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 5rem;
    @include m.tablet {
        margin-top: 0;
    }
    h2{
        font-weight: 900;
        margin-bottom: 1rem;
    }
    h2::first-letter{
        color: v.$amarillo;
    }
    .fecha{
        color: v.$rosa;
        font-weight: 700;
    }
}